<!-- eslint-disable -->
<template>
  <div class="perguntas-wrapper">
    <h1>Perguntas Frequentes</h1>
    <div class="perguntas">
      <div class="left">
        <div class="topics">
          <div class="topic" v-for="t in topicos" :key="t.id" @click="goToTopic(t.id)">
            <div class="text">{{t.topic}}</div>
          </div>
        </div>
      </div>
      <div class="right">
        <div
          class="topic-wrapper"
          v-for="(t, index) in topicos"
          :key="t.id"
          :class="{'active': isActive}"
          :data-topico-id="t.id"
        >
          <!-- <div class="header">
            <div class="text">{{t.topic}}</div>
          </div>-->
          <div class="content">
            <div class="question" v-for="p in topicos[index].perguntas" :key="p.id">
              <div class="quest" @click="openQuestion(p.id)" :data-topic-id="p.id">
                <div class="text">{{p.pergunta}}</div>
                <div class="icon">
                  <svg
                    class="arrow"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlns:xlink="http://www.w3.org/1999/xlink"
                    viewBox="0 0 4.743 7.308"
                  >
                    <defs>
                      <clipPath id="a">
                        <rect width="4.743" height="7.308" fill="none" />
                      </clipPath>
                    </defs>
                    <g clip-path="url(#a)">
                      <path
                        d="M3.654,4.743,0,1.088,1.088,0,3.654,2.566,6.22,0,7.308,1.088Z"
                        transform="translate(0 7.308) rotate(-90)"
                        fill="#707070"
                      />
                    </g>
                  </svg>
                </div>
              </div>
              <div class="answer">
                <div v-html="p.resposta" class="text"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  data() {
    return {
      isActive: false,
      topicos: [
        {
          id: "1",
          topic: "Perguntas Frequentes",
          perguntas: [
            {
              id: "1",
              pergunta: "Como posso contactar os vossos serviços?",
              resposta:
                "Poderá contactar-nos através do nosso contacto telefónico, email ou através da nossa plataforma digital (XXXX). Este primeiro contacto será, sem dúvida, muito importante uma vez que possibilitará que possamos esclarecer quaisquer dúvidas que possam surgir bem como permitirá que possamos transmitir as diretrizes de determinado procedimento clínico e, assim, estabelecer as bases que sustentarão um exercício do ato clínico assente na transparência da comunicação, no consentimento informado, no rigor dos procedimentos clínicos que devem balizar a atividade psicológica e, sobretudo, no respeito e confiança de quem se nos confia.",
            },
            {
              id: "2",
              pergunta: "O que faz um Psicólogo?",
              resposta:
                "No caso específico da área clínica/psicoterapêutica, um psicólogo com formação clínica pretende, mediante técnica especializada, no âmbito da relação clínica, identificar e interpretar as circunstâncias/os contextos/os “ruídos” emocionais que possam estar a condicionar a saúde emocional de uma pessoa, com traduções nas mais diversas áreas da vida. Pretende, igualmente, e partindo das dinâmicas de funcionamento mental e vida relacional, promover um espaço de significações, re(significações), legendas dos potencias “ruídos emocionais”, de pensar a dor que, por vezes, acaba por somente encontra “lugar” de ser manifestada no espaço do corpo (e que provoca, ainda, mais dor) ou em sinais e sintomas diversos que, sublinhe-se, nunca serão o ponto de chegada de um acompanhamento psicoterapêutico. Mas o seu ponto de partida. Para, na relação clínica, se poder pensar o não pensado. A partir deles. Mas para além deles. Com o objetivo, sempre presente, que a pessoa que se encontra nesse espaço clínico (concebido na sua dimensão relacional) possa, ao legendar o ruído, daí conseguir retirar contrapartidas práticas para a sua vida. E, nesse mesmo espaço clínico, o terapeuta possa, igualmente, olhar para dentro de si. Como técnico. E, como tal, como pessoa. Porque uma relação clínica nunca será um exercício unilateral ou de escuta passiva. Será, sempre, um espaço de construção. (Re)construção. Significação. Em ambas as direções.",
            },
            {
              id: "3",
              pergunta:
                "O que distingue as especificidades de um psicólogo clínico e de um psiquiatra?",
              resposta:
                "A psicologia e a psiquiatria constituem domínios da ciência subsidiárias de formações académicas distintas, ainda que, como denominador comum, versem sobre a promoção da saúde mental. Mais especificamente, o psicólogo clínico, em particular, é formado em psicologia clínica, sendo que o psiquiatra é formado em medicina, com uma especialização em psiquiatra. O psicólogo clínico, na sua prática clínica, é especialista nos domínios de atuação que diferem de um psiquiatra, nomeadamente ao nível dos preceitos teórico-práticos e conceptuais, técnicas de diagnóstico, instrumentos e metodologia de avaliação e intervenção psicoterapêutica. A psiquiatria é paritária, para além de todos os considerandos teóricos e clínicos que balizam o seu ramo de especialização, do recurso a prescrição medicamentosa, não estando essa competência na alçada de um psicólogo, especificamente pelo que os separa em termos de formação académica. Seja como for, a psicologia e a psiquiatria, bem como outras áreas do conhecimento científico, não deverão ser concebidas como valências profissionais antagónicas e não comunicantes, pelo contrário. Quão mais se estreitar a ciência e se possibilitar a aprendizagem com os mais diversos conhecimentos científicos, tão mais contrapartidas daí surgirão. Para nós, os profissionais. Para nós, as pessoas que recorrem à seriedade dos profissionais de saúde mental que, mais do que, como acontece, por vezes, arreigados a posições deterministas, fundamentalistas na (re)afirmação das suas competências (em detrimento de todas as demais áreas do conhecimento, por vezes), comungam de algo que os aproxima: a ciência e o conhecimento e o modo como, nessa interdependência, se poderá promover, numa abordagem multidisciplinar, a saúde mental e familiar.",
            },
            {
              id: "4",
              pergunta: "Confidencialidade e a privacidade serão asseguradas?",
              resposta:
                "Sim, sem dúvida. Na verdade, e regendo-nos por uma Ordem dos Psicólogos Profissionais que, entre as mais-valias que tem acrescentado ao rigor e seriedade da legitimação e afirmação da nossa profissão, elaborou um código deontológico específico que deverá ser respeitado. E, se por hipótese, não dispuséssemos desse mesmo Código, asseguraríamos, ainda assim, o respeito pela confidencialidade e privacidade de quem a nós recorre. Até porque o exercício da nossa atividade clínica não poderia dissociar-se, jamais, dessa premissa de respeito pelo outro. Em síntese, o psicólogo clínico terá o dever, ético e deontológico, de assegurar a confidencialidade e privacidade em contexto clínico. E esse contexto clínico começará, desde logo, aquando de um primeiro contacto (seja por telefone, por email ou nas plataformas sociais que dispusermos para contacto). Prosseguindo a cada ato clínico que, mediante consentimento informado, se vier a realizar. Com efeito, sem esse mesmo consentimento explícito por parte de quem a nós recorre, temos o dever, uma vez mais, ético e deontológico, de não veicularmos qualquer tipo de informação a outrem (seja num primeiro contato ou no decorrer do processo de intervenção clínica). A esta dinâmica excetuar-se-ão as circunstâncias de eminente risco e/ou perigo de vida imediato para a própria pessoa, ou para terceiros, que possa ameaçar, de uma forma grave, a sua integridade física ou psíquica. Nestas circunstâncias, o dever de proteger o bem essencial – a pessoa – e a segurança, física e psíquica, de quem a nós recorre, ou de terceiros, sobrepor-se-á ao dever de proteção da confidencialidade e privacidade, sob risco de, se assim não procedêssemos, o exercício clínico poder redundar num ato clínico abstrato, sem diretrizes éticas demarcadas e sem guidelines que a ninguém protegeria.",
            },
            {
              id: "5",
              pergunta: "Qual o tipo de procedimento clínico mais indicado?",
              resposta:
                "Neste caso em específico, enquanto profissionais na área de saúde mental privilegiaremos um contato atento e disponível e que procure corresponder, no exercício da nossa função, às expetativas de quem procura serviços especializados. Assim, mediante as preocupações evidenciadas e indicadores clínicos relevantes emergentes num primeiro contacto (preferencialmente, por contato telefónico), procuramos explicar o procedimento clínico mais adequado e todas as subsequentes diligências clínicas – sempre no espaço relacional que prima pela transparência e confiança na relação – disponibilizando-nos, sempre, para corresponder a quaisquer esclarecimentos que possam ocorrer.",
            },
            {
              id: "6",
              pergunta: "Modalidades de Pagamento",
              resposta:
                "No que se refere às modalidades de pagamento, poderá proceder ao pagamento mediante numerário ou através de terminal de pagamento automático (vulgo, TPA). Em circunstâncias mais específicas, poder-se-á realizar o pagamento mediante transferência bancária. ",
            },
          ],
        },
        // {
        //   id: "2",
        //   topic: "topico 2",
        //   perguntas: [
        //     {
        //       id: "3",
        //       pergunta: "pergunta 1 topico 2",
        //       resposta: "resposta 1 topico 2"
        //     },
        //     {
        //       id: "4",
        //       pergunta: "pergunta 2 topico 2",
        //       resposta: "resposta 2 topico 2"
        //     },
        //     {
        //       id: "5",
        //       pergunta: "pergunta 3 topico 2",
        //       resposta: "resposta 3 topico 2"
        //     }
        //   ]
        // }
      ],
    };
  },
  methods: {
    openQuestion(id) {
      var pergunta = $('*[data-topic-id="' + id + '"]');

      $(".quest").css("font-weight", "400");
      $(".arrow").removeClass("rotate");
      if (pergunta.parent().find(".answer").hasClass("active")) {
        $(".active").slideUp("slow");
        $(".active").removeClass("active");
      } else {
        $(".active").slideUp("slow");
        $(".active").removeClass("active");
        pergunta.parent().find(".answer").slideDown("slow");
        pergunta.css("font-weight", "bold");
        pergunta.parent().find(".answer").addClass("active");
        pergunta.children().find(".arrow").addClass("rotate");
      }
    },
    goToTopic(id) {
      var topico = $('*[data-topico-id="' + id + '"]');
      if (topico.hasClass("opened")) {
        topico.removeClass("opened");
      } else {
        $("*[data-topico-id]").removeClass("opened");
        topico.addClass("opened");
      }
    },
  },
  mounted() {
    this.openQuestion();
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquoise: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.perguntas-wrapper {
  background-color: $white;
  height: auto;
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  @media only screen and (max-width: 599px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  h1 {
    margin-top: pxToEm(100);
    text-align: center;
    color: $turquoise;
  }

  .perguntas {
    display: flex;
    height: 100%;
    justify-content: space-between;
    margin: pxToEm(50) auto;
    max-width: pxToEm(1400);
    width: 100%;

    @media only screen and (max-width: 991px) {
      flex-direction: column;
    }

    > div {
      @media only screen and (max-width: 991px) {
        width: 100%;
      }
    }

    .left {
      display: flex;
      flex-direction: column;
      align-items: center;

      .topics {
        width: pxToEm(400);

        @media only screen and (max-width: 991px) {
          width: 100%;
        }

        .topic {
          width: 100%;
          height: pxToEm(50);
          background-color: $turquoise;
          margin-bottom: pxToEm(10);
          display: flex;
          align-items: center;
          justify-content: flex-start;
          border-radius: pxToEm(10);
          cursor: pointer;

          .text {
            color: $white;
            font-size: pxToEm(20);
            margin-left: pxToEm(20);
          }
        }
      }
    }

    .right {
      .topic-wrapper {
        width: pxToEm(800);
        display: none;

        @media only screen and (max-width: 991px) {
          width: 100%;
        }

        &.opened {
          display: block;
        }

        .header {
          height: pxToEm(50);
          background-color: $turquoise;
          display: flex;
          align-items: center;
          justify-content: flex-start;
          border-radius: pxToEm(10);

          @media only screen and (max-width: 991px) {
            display: none;
          }

          .text {
            color: $white;
            font-size: pxToEm(20);
            margin-left: pxToEm(20);
          }
        }

        .content {
          padding: 0 pxToEm(20);

          .question {
            margin: pxToEm(20) 0;
            overflow: hidden;
            border-bottom: pxToEm(1) solid $dk-gray-3;
            transition: max-height 2s;
            user-select: none;

            &:last-child {
              border: none;
            }

            &.open {
              max-height: pxToEm(600);
            }

            .quest {
              height: pxToEm(60);
              display: flex;
              align-items: center;
              justify-content: space-between;
              cursor: pointer;

              .text {
                font-size: pxToEm(20);
              }

              .icon {
                width: pxToEm(20);
                height: pxToEm(20);
                border-radius: $circle;
                border: pxToEm(1) solid $dk-gray-3;
                display: flex;
                align-items: center;
                justify-content: center;

                .arrow {
                  width: pxToEm(4.7);
                  height: pxToEm(7.23);
                  transform: rotate(90deg);

                  &.rotate {
                    transform: rotate(-90deg);
                  }
                }
              }
            }

            .answer {
              margin: pxToEm(20) 0;
              font-size: pxToEm(16);
              line-height: pxToEm(30);
              display: none;

              .text {
                font-size: pxToEm(16);
              }
            }
          }
        }
      }
    }
  }
}
</style>



